:root{--primary:#22543d;--primary-light:#48bb78;--secondary:#2b6cb0;--accent:#d69e2e;--background:#f0fff4;--text:#1a202c;--gray-100:#f7fafc;--gray-200:#edf2f7;--gray-300:#e2e8f0;--gray-600:#718096;--shadow-sm:0 2px 4px #0000000d;--shadow:0 4px 6px #0000001a;--shadow-lg:0 10px 20px #00000026}*,:after,:before{box-sizing:border-box;margin:0;padding:0}body{font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,sans-serif;background:var(--background);color:var(--text);line-height:1.6}.container{max-width:1280px;margin:0 auto;padding:0 1.5rem}.header{background:#fff;box-shadow:var(--shadow);position:sticky;top:0;z-index:50;-webkit-backdrop-filter:blur(8px);backdrop-filter:blur(8px);border-bottom:1px solid var(--gray-200)}.nav{height:4rem;display:flex;align-items:center;justify-content:space-between}.logo{font-size:1.5rem;font-weight:700;background:linear-gradient(135deg,var(--primary) 0,var(--primary-light) 100%);-webkit-background-clip:text;-webkit-text-fill-color:#0000;letter-spacing:-.5px}.nav-links{display:flex;gap:2.5rem;align-items:center}.nav-link{font-weight:500;color:var(--text);text-decoration:none;position:relative;padding:.5rem 0}.nav-link:after{content:"";position:absolute;bottom:0;left:0;width:0;height:2px;background:var(--primary);transition:width .3s ease}.nav-link:hover:after{width:100%}.nav-link.active{color:var(--primary)}.auth-button{background:var(--primary);color:#fff;padding:.75rem 1.5rem;border-radius:9999px;font-weight:600;border:none;cursor:pointer;transition:transform .2s ease,box-shadow .2s ease}.auth-button:hover{transform:translateY(-2px);box-shadow:var(--shadow)}.hero{padding:6rem 0;text-align:center;background:linear-gradient(135deg,var(--gray-100) 0,#fff 100%)}.hero h1{font-size:4rem;font-weight:800;line-height:1.1;background:linear-gradient(135deg,var(--primary) 0,var(--primary-light) 100%);-webkit-background-clip:text;-webkit-text-fill-color:#0000;margin-bottom:1.5rem}.hero p{font-size:1.25rem;color:var(--gray-600);max-width:600px;margin:0 auto 3rem}.products-grid{display:grid;grid-template-columns:repeat(auto-fill,minmax(320px,1fr));gap:2rem;padding:2rem 0}.product-card{background:#fff;border-radius:1rem;overflow:hidden;transition:all .3s ease;border:1px solid var(--gray-200)}.product-card:hover{transform:translateY(-8px) scale(1.01);box-shadow:var(--shadow-lg)}.product-image{aspect-ratio:16/9;overflow:hidden}.product-image img{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;transition:transform .5s ease}.product-card:hover .product-image img{transform:scale(1.1)}.product-info{padding:1.5rem}.product-title{font-size:1.25rem;font-weight:600;margin-bottom:.5rem;color:var(--text)}.product-price{font-size:1.5rem;font-weight:700;color:var(--primary);margin:1rem 0}.product-rating{display:flex;align-items:center;gap:.5rem}.stars{color:var(--accent)}.review-card{background:#fff;border-radius:1rem;padding:2rem;margin-bottom:1.5rem;box-shadow:var(--shadow-sm);border:1px solid var(--gray-200)}.review-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:1rem;padding-bottom:1rem;border-bottom:1px solid var(--gray-200)}@media (max-width:768px){.nav{height:auto;padding:1rem 0}.nav,.nav-links{flex-direction:column}.nav-links{width:100%;gap:1rem;margin-top:1rem}.hero h1{font-size:2.5rem}.products-grid{grid-template-columns:1fr}}@keyframes fadeIn{0%{opacity:0;transform:translateY(20px)}to{opacity:1;transform:translateY(0)}}.fade-in{animation:fadeIn .6s ease forwards}